import React, { PureComponent } from "react";
import { isTagShop } from "../../../../actions/api";
import parse from "html-react-parser";
import { STRING_TO_URL_CONVERT } from "../../../../utils/Common";
// import PostContentHighlight from "./PostContentHighlight";

export default class PostContent extends PureComponent {
  constructor(props) {
    super(props);
    this.contentRef = React.createRef();
  }
  componentDidMount() {
      setTimeout(() => {
        if (this.contentRef?.current?.clientHeight) {
          const contentHeight = this.contentRef?.current?.clientHeight || 0;
          if (this.props.contentRefCallBack) this.props.contentRefCallBack(contentHeight)
        }
      }, 100);

    //  console.log(parsedHTML); // Parsed structure
  }

  render() {
    const { CardStyle, contentClass, content, contentTitle, maxLines, isReadMore } = this.props;
    const trimLines = CardStyle.content.trimcontent && !isTagShop ? CardStyle.content.trimcontent : 0;
    const truncateStyle = {
      WebkitLineClamp: maxLines,
      textAlign: CardStyle.style.textAlign
    };
    const contentData = STRING_TO_URL_CONVERT(content);
    const trimClasses = `${trimLines ? ` tb_content_line-${trimLines}` : ``}`
    const contentClasses = `${contentClass} ${!isReadMore ? trimClasses : ''} tb-cTBfont-${CardStyle.style.variant}`
    return (
      <div className={contentClasses} ref={this.contentRef} style={truncateStyle}>
        {contentTitle ? <div className="tb_bold_txt__">{parse(contentTitle)}</div> : null}
        {parse(contentData)}
        {/* <PostContentHighlight content={contentData} CardStyle={CardStyle} /> */}
      </div>
    );
  }
}
